//- Load Fonts
@charset "UTF-8";
/*********************
1. WEB FONTS
*********************/

@font-face {
  font-family: 'Manifold DSA';
  font-display: fallback;
  src: url('../../fonts/ManifoldDSA-Bold.woff2') format('woff2'),
    /* Super Modern Browsers */
    url('../../fonts/ManifoldDSA-Bold.woff') format('woff'),
    /* Pretty Modern Browsers */
    url('../../fonts/ManifoldDSA-Bold.ttf') format('truetype');
  /* Safari, Android, iOS */
  font-weight: bold;
  font-weight: 600;

}


@font-face {
  font-family: 'Manifold DSA';
  font-display: fallback;
  src: url('../../fonts/ManifoldDSA-Medium.woff2') format('woff2'),
    /* Super Modern Browsers */
    url('../../fonts/ManifoldDSA-Medium.woff') format('woff'),
    /* Pretty Modern Browsers */
    url('../../fonts/ManifoldDSA-Medium.ttf') format('truetype');
  /* Safari, Android, iOS */
  font-weight: normal;
  font-weight: 400;
  font-style: normal;
}